import { Autoplay, Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/autoplay';

import bannerImage1 from "../../images/banners/1.png"
import bannerImage2 from "../../images/banners/2.png"
import bannerImage3 from "../../images/banners/3.png"

const bannerImages = [
  {
    src: bannerImage1,
    alt: "banner 1"
  },
  {
    src: bannerImage2,
    alt: "banner 2"
  },
  {
    src: bannerImage3,
    alt: "banner 3"
  },
]

const CarouselBanner = () => {
  return (
    <Swiper
      modules={[Autoplay, Pagination, Navigation]}
      navigation
      pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}
      autoplay={{
        delay: 3000,
        disableOnInteraction: false,
      }}
      spaceBetween={50}
      style={{ width: "100%" }}
    >
      {
        bannerImages.map((banner, index) => (
          <SwiperSlide key={banner.src} >
            <img width="100%" src={banner.src} alt={banner.alt} />
          </SwiperSlide>
        ))
      }
    </Swiper>
  )
};

export default CarouselBanner;